@import '../includes';;

header {
    margin-bottom: 5rem;
}

footer {
    margin-top: 4rem;
}

.modal-header {
    padding: 10px;
    justify-content: flex-end;
    button {
        border: none;
        background: transparent;
    }
    .close {
        font-size: 1.6rem;
        background: transparent;
    }
}

.modal-body {
    ul {
        list-style: none;
    }

    li {
        font-size: 1.2rem;
        margin-bottom: 10px;
    }
}


.tabulator .tabulator-footer .tabulator-page.active {
  color: #0d6efd;
}

[ aria-label="First Page"], [ aria-label="Prev Page"], [ aria-label="Next Page"], [ aria-label="Last Page"] {
    display: none!important;
}

.tabulator-row .tabulator-cell {
    font-size: 1rem;
    padding: 10px 21px;
    font-weight: 600;
}

.modal-body li  {
    color: #0d6efd;
    font-size: 1.2rem;

    span {
        color: black;
        margin-right: 1rem;
    }
}